<template>
<div id="single" v-theme:column="'narrow'" >
    <div class="page-header">
    <h1>{{blogs.title}}</h1>
    <article>{{blogs.contect}}</article>
    <p>{{blogs.author}}</p>
</div>

</div>
</template>
<script>
export default{
    name:'',
    data(){
        return{
            id:this.$route.params.id,
            blogs:{}
        }

    },
    created(){
        this.$http.get('https://wd1191931990wfedcx.wilddogio.com/posts/'+this.id+'.json').then((data)=>{
            console.log(data);
            return data.json();

            // this.blogs=data.body;
        }).then(function(data){
            this.blogs=data;
        })
    }

    
}






</script>
<style>
#single{
    margin:0 auto;
    padding:20px;
}
</style>







</style>



