<template>
  <div id="show" class="container" v-theme:column="'narrow'">
 <h1 >博客总览</h1>
 <input type="text" v-model="search" class="input-lg" placeholder="搜索">
 <div class="page-header well" v-for='list in filterBox' >
    <router-link v-bind:to="'./blog/'+list.id"><h3 v-rainbow>{{list.title|to-uppercase}}</h3></router-link>
    <article>{{list.contect}}</article>
</div>
  </div>
</template>

<script>


export default {
  name: 'show',
  data(){
      return{
          lists:[],
          search:''

      }
  },
  created(){
      this.$http.get('https://wd1191931990wfedcx.wilddogio.com/posts.json')
      .then(function(data){
          // console.log(data.json());
          // this.lists=data.body.slice(0,10);
          // console.log(this.lists)
          return data.json()
      })
      .then(function(data){
          var blogsArry=[];
          for(let key in data){
            // console.log(key)
            // console.log(data[key])
            data[key].id=key;
            blogsArry.push(data[key])
            // console.log(blogsArry);
            this.lists=blogsArry;
            // console.log(this.lists)
          }
        
      })
  },
  computed:{
    filterBox:function(){
      return this.lists.filter((list)=>{
        return list.title.match(this.search)
      })
    }
  }
}
</script>

<style scoped>
input{
  width:100%
}
</style>


