<template>
  <div id="add" class="container" v-theme:column="'narrow'">
    <h2 v-if="!ok">写博客</h2>
    <form role="form" v-if="!ok" >
      <div class="form-group">
        <label for="name">博客主题</label>
        <input type="text" class="form-control" id="name" placeholder="请输入名称" v-model="arr.title">
      </div>
      <div class="form-group">
        <label for="name">博客内容</label>
        <textarea class="form-control" rows="8" v-model="arr.contect"></textarea>
      </div>
      <label for="name">博客分类</label>
      <div class="form-group">
        <label class="checkbox-inline">
          <input type="checkbox" value="Vue" v-model="arr.list"> Vue
        </label>
        <label class="checkbox-inline">
          <input type="checkbox" value="Html" v-model="arr.list"> Html
        </label>
        <label class="checkbox-inline">
          <input type="checkbox" value="Css" v-model="arr.list"> Css
        </label>
        <label class="checkbox-inline">
          <input type="checkbox" value="Css3" v-model="arr.list"> Css3
        </label>
      </div>

      <label for="name">作者：</label>
      <select class="form-control" v-model="arr.author">
        <option v-for="author in arr.authors">{{author}}</option>
      </select>
      <button type="submit" class="btn btn-lg" v-on:click.prevent="submits">添加博客</button>
    </form>
    <div v-if="ok">您已经添加成功</div>

    <div class="show well" v-if="ok">
      <h3>博客总览</h3>

      <p>博客标题：{{arr.title}}</p>
      <p>博客内容：{{arr.contect}}</p>
      <p>博客分类：</p>
      <ul class="list-unstyled list-inline">
        <li v-for="item in arr.list">{{item}}</li>
      </ul>
      <p>作者：{{arr.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "add",
  data() {
    return {
      arr: {
        title: "",
        contect: "",
        author: "",
        authors: ["韩宝", "韩媛", "韩荣", "韩群芳"],
        list: [],
      },
      ok: false
    };
  },
  methods: {
    submits() {
      this.$http
        .post("https://wd1191931990wfedcx.wilddogio.com/posts.json",this.arr)
        .then(function(data) {
          console.log(data);
          this.ok = true; //将填写的博客内容隐藏起来
        });
    }
  }
};
</script>


<style scoped>
.btn {
  background:#ddd;
  margin-top: 20px;
  color:#424242;
}
</style>


